import { Injectable, Injector } from '@angular/core';
import { IRecordDefinitionModel, IRecordFieldDefinitionModel } from '../../record-designer.types';
import { RxBaseFieldDefinitionService } from './base-field-definition.service';
import { IValidationIssue, ValidationIssueType } from '@helix/platform/ui-kit';
import { IFormBuilderConfig } from '@helix/platform/shared/api';
import { CounterFormControlComponent, ICounterFormControlOptions } from '@helix/platform/shared/components';
import { isNil, omitBy } from 'lodash';
import { RxNumberUtilsService } from '@helix/platform/utils';
import { RX_RECORD_DEFINITION } from '@helix/platform/record/api';

@Injectable({
  providedIn: 'root'
})
export abstract class RxNumericFieldDefinitionService extends RxBaseFieldDefinitionService {
  abstract readonly minPrecision: number;
  abstract readonly maxPrecision: number;
  abstract readonly minValue: number;
  abstract readonly maxValue: number;
  abstract readonly allowOnlyInteger: boolean;

  constructor(injector: Injector, private rxNumberUtilsService: RxNumberUtilsService) {
    super(injector);
  }

  validate(fieldModel: IRecordFieldDefinitionModel, definitionModel: IRecordDefinitionModel): IValidationIssue[] {
    const validationIssues = super.validate(fieldModel, definitionModel);

    if (fieldModel.defaultValue && Number(fieldModel.defaultValue) < this.minValue) {
      validationIssues.push({
        type: ValidationIssueType.Error,
        description: this.translateService.instant(
          'com.bmc.arsys.rx.client.designer.validation.value-is-too-small-error.message',
          {
            propertyName: this.translateService.instant('com.bmc.arsys.rx.client.common.default-value.label'),
            minValue: this.minValue
          }
        ),
        data: {
          propertyName: 'defaultValue',
          guid: fieldModel.guid
        }
      });
    }

    if (fieldModel.defaultValue && Number(fieldModel.defaultValue) > this.maxValue) {
      validationIssues.push({
        type: ValidationIssueType.Error,
        description: this.translateService.instant(
          'com.bmc.arsys.rx.client.designer.validation.value-is-too-large-error.message',
          {
            propertyName: this.translateService.instant('com.bmc.arsys.rx.client.common.default-value.label'),
            maxValue: this.maxValue
          }
        ),
        data: {
          propertyName: 'defaultValue',
          guid: fieldModel.guid
        }
      });
    }

    if (fieldModel.minValue && Number(fieldModel.minValue) < this.minValue) {
      validationIssues.push({
        type: ValidationIssueType.Error,
        description: this.translateService.instant(
          'com.bmc.arsys.rx.client.designer.validation.value-is-too-small-error.message',
          {
            propertyName: this.translateService.instant(
              'com.bmc.arsys.rx.client.designer.field-properties.integer-min-value.label'
            ),
            minValue: this.minValue
          }
        ),
        data: {
          propertyName: 'minValue',
          guid: fieldModel.guid
        }
      });
    }

    if (fieldModel.minValue && Number(fieldModel.minValue) > this.maxValue) {
      validationIssues.push({
        type: ValidationIssueType.Error,
        description: this.translateService.instant(
          'com.bmc.arsys.rx.client.designer.validation.value-is-too-large-error.message',
          {
            propertyName: this.translateService.instant(
              'com.bmc.arsys.rx.client.designer.field-properties.integer-min-value.label'
            ),
            maxValue: this.maxValue
          }
        ),
        data: {
          propertyName: 'minValue',
          guid: fieldModel.guid
        }
      });
    }

    if (fieldModel.maxValue && Number(fieldModel.maxValue) < this.minValue) {
      validationIssues.push({
        type: ValidationIssueType.Error,
        description: this.translateService.instant(
          'com.bmc.arsys.rx.client.designer.validation.value-is-too-small-error.message',
          {
            propertyName: this.translateService.instant(
              'com.bmc.arsys.rx.client.designer.field-properties.integer-max-value.label'
            ),
            minValue: this.minValue
          }
        ),
        data: {
          propertyName: 'maxValue',
          guid: fieldModel.guid
        }
      });
    }

    if (fieldModel.maxValue && Number(fieldModel.maxValue) > this.maxValue) {
      validationIssues.push({
        type: ValidationIssueType.Error,
        description: this.translateService.instant(
          'com.bmc.arsys.rx.client.designer.validation.value-is-too-large-error.message',
          {
            propertyName: this.translateService.instant(
              'com.bmc.arsys.rx.client.designer.field-properties.integer-max-value.label'
            ),
            maxValue: this.maxValue
          }
        ),
        data: {
          propertyName: 'maxValue',
          guid: fieldModel.guid
        }
      });
    }

    if (
      this.rxNumberUtilsService.isFiniteOrNumberString(fieldModel.defaultValue) &&
      this.rxNumberUtilsService.isFiniteOrNumberString(fieldModel.minValue) &&
      Number(fieldModel.defaultValue) >= this.minValue &&
      Number(fieldModel.defaultValue) < Number(fieldModel.minValue)
    ) {
      validationIssues.push({
        type: ValidationIssueType.Error,
        description: this.translateService.instant(
          'com.bmc.arsys.rx.client.designer.validation.value-is-too-small-error.message',
          {
            propertyName: this.translateService.instant('com.bmc.arsys.rx.client.common.default-value.label'),
            minValue: this.translateService.instant(
              'com.bmc.arsys.rx.client.designer.field-properties.integer-min-value.label'
            )
          }
        ),
        data: {
          propertyName: 'defaultValue',
          guid: fieldModel.guid
        }
      });
    }

    if (
      this.rxNumberUtilsService.isFiniteOrNumberString(fieldModel.defaultValue) &&
      this.rxNumberUtilsService.isFiniteOrNumberString(fieldModel.maxValue) &&
      Number(fieldModel.defaultValue) <= this.maxValue &&
      Number(fieldModel.defaultValue) > Number(fieldModel.maxValue)
    ) {
      validationIssues.push({
        type: ValidationIssueType.Error,
        description: this.translateService.instant(
          'com.bmc.arsys.rx.client.designer.validation.value-is-too-large-error.message',
          {
            propertyName: this.translateService.instant('com.bmc.arsys.rx.client.common.default-value.label'),
            maxValue: this.translateService.instant(
              'com.bmc.arsys.rx.client.designer.field-properties.integer-max-value.label'
            )
          }
        ),
        data: {
          propertyName: 'defaultValue',
          guid: fieldModel.guid
        }
      });
    }

    if (
      this.rxNumberUtilsService.isFiniteOrNumberString(fieldModel.minValue) &&
      this.rxNumberUtilsService.isFiniteOrNumberString(fieldModel.maxValue) &&
      Number(fieldModel.minValue) > Number(fieldModel.maxValue)
    ) {
      validationIssues.push({
        type: ValidationIssueType.Error,
        description: this.translateService.instant(
          'com.bmc.arsys.rx.client.designer.validation.value-is-too-large-error.message',
          {
            propertyName: this.translateService.instant(
              'com.bmc.arsys.rx.client.designer.field-properties.integer-min-value.label'
            ),
            maxValue: this.translateService.instant(
              'com.bmc.arsys.rx.client.designer.field-properties.integer-max-value.label'
            )
          }
        ),
        data: {
          propertyName: 'minValue',
          guid: fieldModel.guid
        }
      });
    }

    if (isNil(fieldModel.minValue)) {
      validationIssues.push({
        type: ValidationIssueType.Error,
        description: this.translateService.instant(
          'com.bmc.arsys.rx.client.designer.validation.cannot-be-blank.message',
          {
            propertyName: this.translateService.instant(
              'com.bmc.arsys.rx.client.designer.field-properties.integer-min-value.label'
            )
          }
        ),
        data: {
          propertyName: 'minValue',
          guid: fieldModel.guid
        }
      });
    }

    if (isNil(fieldModel.maxValue)) {
      validationIssues.push({
        type: ValidationIssueType.Error,
        description: this.translateService.instant(
          'com.bmc.arsys.rx.client.designer.validation.cannot-be-blank.message',
          {
            propertyName: this.translateService.instant(
              'com.bmc.arsys.rx.client.designer.field-properties.integer-max-value.label'
            )
          }
        ),
        data: {
          propertyName: 'maxValue',
          guid: fieldModel.guid
        }
      });
    }

    if (this.resourceType !== RX_RECORD_DEFINITION.dataTypes.integer.resourceType && isNil(fieldModel.precision)) {
      validationIssues.push({
        type: ValidationIssueType.Error,
        description: this.translateService.instant(
          'com.bmc.arsys.rx.client.designer.validation.cannot-be-blank.message',
          {
            propertyName: this.translateService.instant(
              'com.bmc.arsys.rx.client.record-designer.field-properties.precision.label'
            )
          }
        ),
        data: {
          propertyName: 'maxValue',
          guid: fieldModel.guid
        }
      });
    }

    return validationIssues;
  }

  getFieldInspectorConfig(
    fieldModel: IRecordFieldDefinitionModel,
    definitionModel: IRecordDefinitionModel,
    isReadOnly: boolean
  ): IFormBuilderConfig {
    const inspectorConfig = super.getFieldInspectorConfig(fieldModel, definitionModel, isReadOnly);

    // Adding controls specific to decimal field to Details section
    inspectorConfig[1].controls = inspectorConfig[1].controls.concat([
      {
        name: 'precision',
        component: CounterFormControlComponent,
        isDisabled: isReadOnly,
        options: omitBy(
          {
            label: this.translateService.instant(
              'com.bmc.arsys.rx.client.record-designer.field-properties.precision.label'
            ),
            required: true,
            allowIntegerOnly: true,
            minValue: this.minPrecision,
            maxValue: this.maxPrecision
          },
          isNil
        ) as ICounterFormControlOptions
      },

      {
        name: 'minValue',
        component: CounterFormControlComponent,
        isDisabled: isReadOnly,
        options: {
          label: this.translateService.instant(
            'com.bmc.arsys.rx.client.designer.field-properties.integer-min-value.label'
          ),
          required: true,
          allowIntegerOnly: this.allowOnlyInteger,
          minValue: this.minValue,
          maxValue: this.maxValue
        } as ICounterFormControlOptions
      },
      {
        name: 'maxValue',
        component: CounterFormControlComponent,
        isDisabled: isReadOnly,
        options: {
          label: this.translateService.instant(
            'com.bmc.arsys.rx.client.designer.field-properties.integer-max-value.label'
          ),
          required: true,
          allowIntegerOnly: this.allowOnlyInteger,
          minValue: this.minValue,
          maxValue: this.maxValue
        } as ICounterFormControlOptions
      },
      {
        name: 'defaultValue',
        component: CounterFormControlComponent,
        isDisabled: isReadOnly,
        options: {
          label: this.translateService.instant('com.bmc.arsys.rx.client.common.default-value.label'),
          allowIntegerOnly: this.allowOnlyInteger,
          minValue: this.minValue,
          maxValue: this.maxValue
        } as ICounterFormControlOptions
      }
    ]);

    return inspectorConfig;
  }
}
